"use client";
import { useEffect, useRef, useState } from "react";
import { useParams } from "next/navigation";
import { Card } from "../../../components/ui/card";
import { Button } from "../../../components/ui/button";

interface LocationData {
  latitude: number;
  longitude: number;
}

export default function TrackingPage() {
  const { trackingId } = useParams() as { trackingId: string };
  const videoRef = useRef<HTMLVideoElement>(null);
  const canvasRef = useRef<HTMLCanvasElement>(null);

  const [consent, setConsent] = useState(false);
  const [photo, setPhoto] = useState<string | null>(null);
  const [location, setLocation] = useState<LocationData | null>(null);
  const [error, setError] = useState("");
  const [submitted, setSubmitted] = useState(false);

  useEffect(() => {
    if (!consent) return;

    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (pos) => {
          setLocation({
            latitude: pos.coords.latitude,
            longitude: pos.coords.longitude,
          });
        },
        () => {
          setError("Geolocation permission denied or unavailable.");
        }
      );
    } else {
      setError("Geolocation is not supported by this browser.");
    }

    async function startCamera() {
      try {
        const stream = await navigator.mediaDevices.getUserMedia({
          video: { facingMode: "user" },
          audio: false,
        });
        if (videoRef.current) {
          videoRef.current.srcObject = stream;
          videoRef.current.play();
        }
      } catch {
        setError("Camera permission denied or unavailable.");
      }
    }
    startCamera();

    return () => {
      if (videoRef.current && videoRef.current.srcObject) {
        const stream = videoRef.current.srcObject as MediaStream;
        stream.getTracks().forEach((track) => track.stop());
      }
    };
  }, [consent]);

  const capturePhoto = () => {
    if (!videoRef.current || !canvasRef.current) return;
    const video = videoRef.current;
    const canvas = canvasRef.current;
    canvas.width = video.videoWidth;
    canvas.height = video.videoHeight;
    const ctx = canvas.getContext("2d");
    if (ctx) {
      ctx.drawImage(video, 0, 0, canvas.width, canvas.height);
      const dataUrl = canvas.toDataURL("image/png");
      setPhoto(dataUrl);
    }
  };

  const handleSubmit = async () => {
    if (!photo || !location) {
      setError("Photo and location must be captured before submission.");
      return;
    }
    try {
      const res = await fetch("/api/track", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          trackingId,
          photo,
          location,
          userAgent: navigator.userAgent,
          timestamp: new Date().toISOString(),
        }),
      });
      if (!res.ok) throw new Error("Submission failed.");
      setSubmitted(true);
    } catch {
      setError("Failed to submit your data. Please try again.");
    }
  };

  if (!consent) {
    return (
      <main className="min-h-screen bg-black text-white flex items-center justify-center p-6">
        <Card className="p-6 text-center">
          <h1 className="text-2xl font-bold mb-4">Consent Required</h1>
          <p className="mb-4">
            To verify your visit, we require access to your front camera and location. We will capture a photo and your current location only after you have consented.
          </p>
          <Button onClick={() => setConsent(true)}>I Consent</Button>
        </Card>
      </main>
    );
  }

  return (
    <main className="min-h-screen bg-black text-white flex flex-col items-center p-6">
      <h1 className="text-4xl font-bold mb-6">Tracking in Progress</h1>
      {error && <p className="mb-4 text-red-500 font-semibold">{error}</p>}
      {!submitted ? (
        <>
          <div className="mb-6">
            <video
              ref={videoRef}
              className="rounded-lg border-4 border-white max-w-xs w-full"
              autoPlay
              muted
              playsInline
            />
          </div>
          <Button onClick={capturePhoto} className="mb-6">
            Capture Photo
          </Button>
          {photo && (
            <div className="mb-6 flex flex-col items-center">
              <h2 className="text-2xl font-semibold mb-2">Captured Photo</h2>
              <img
                src={photo}
                alt="Captured"
                className="rounded-lg border-4 border-white max-w-xs w-full"
              />
            </div>
          )}
          {location && (
            <div className="text-center mb-6">
              <h2 className="text-2xl font-semibold mb-2">Your Location</h2>
              <p>
                Latitude: {location.latitude.toFixed(6)} <br />
                Longitude: {location.longitude.toFixed(6)}
              </p>
            </div>
          )}
          <Button onClick={handleSubmit}>Submit Data</Button>
          <canvas ref={canvasRef} className="hidden" />
        </>
      ) : (
        <Card className="p-6 text-center">
          <h2 className="text-2xl font-semibold mb-4">Submission Successful</h2>
          <p>Thank you for sharing your data.</p>
        </Card>
      )}
    </main>
  );
}
